.listing_main {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.listing_content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 85%;
    min-height: 55vh;

}

.listing_hero {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 65vh;
    width: 100%;
    padding: 0px 8% 0px 8%;
    margin: 0px 0px 80px 0px;
}

.listing_hero_text {
    display: flex;
    font-weight: bolder;
    font-size: 300%;
    text-align: center;
    flex-direction: column;
    padding: 15px 0px 0px 0px;
}

.listing_hero_searchbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    margin-top: 1%;
    height: 12%;
    width: 60%;
    border: 1px solid black;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

.listing_hero_textinput {
    width: 90%;
    height: 100%;
    outline: none;
    background: none;
}


.listing_design_main {
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 110vh;
    width: 100%;
    padding: 0px 0px 90px 0px;
}
.titulo_hero{
    font-size: 35px;
    padding: 0px 0px 10px 0px;
    font-weight: bold;
}
.listing_design_container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 80%;
    width: 70%;
}

.listing_title {
    width: 90%;
    font-size: 45px;
    font-weight: bolder;
    padding: 0px 30px 0%0%;
}

.listing_text{
    font-size: 20px;
}
.listing_topicos {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: fit-content;
}

.listing_topicos_botao1 {
