.listing_carousel_container {
    display: flex;
    width: 110%;
    flex-direction: column;
    height: 60%;
    margin-bottom: 5vh;
}

.listing_carousel {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80%;
}

.listing_carousel_botao1 {
    background-color: black;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
}

.listing_carousel_botao1:hover {
    cursor: pointer;
}

.cor_botao {
    fill: white;
}

.listing_carousel_title {
    font-size: 15px;
    padding: 0px 0px 90px 0px;
    color:darkgray;
    margin-top: 5vh;

}

.listing_carousel_boxes {
