@media (max-width: 600px) {

  .listing_main {
    padding-top: 54px;
    height: 20%;
    width: 0%;
    flex-direction: column;
    align-items: center;
  }

  .listing_hero {
    height: auto;
    padding: 16px 4vw 24px 9vw;
    margin-bottom: 48px;
    width: 100vw;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .listing_hero_text {
    font-size: 1.1rem;
    padding: 6px 0 0 0;
    line-height: 1.3;
    word-break: break-word;
    text-align: center;
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .listing_hero_searchbar {
    width: 78vw;
    max-width: 420px;
    height: 44px;
    margin: 14px auto 0 auto;
    border-radius: 22px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .listing_design_main {
    height: auto;
    width: 100vw;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .listing_design_container {
    width: 80vw;
    padding: 0 1vw;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .listing_title {
    font-size: 1.6rem;
    padding-top: 18px;
    width: 100%;
    font-weight: bold;
    letter-spacing: 0.5px;
    margin: 0 auto;
  }

  .listing_text {
    font-size: 0.94rem;
    line-height: 1.5;
    padding-top: 5px;
    margin-bottom: 12px;
    width: 100%;
  }

  .listing_topicos {
    flex-direction: column;
    gap: 12px;
    align-items: center;
    padding-top: 18px;
    width: 100%;
  }

  .listing_topicos_botao1,
  .listing_topicos_botao2,
  .listing_topicos_botao3,
  .listing_topicos_botao4 {
    width: 280px;
    padding: 11px 0;
    font-size: 15px;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
    box-sizing: border-box;
    box-shadow: 0 2px 8px rgba(0,0,0,0.04);
    display: flex;
  }

  .listing_carousel_boxes,
  .listing_carousel_boxes2 {
    flex-direction: column;
    gap: 20px;
     padding-right: 50px;
    width: 100vw;
    align-items: center;
    box-sizing: border-box;
    margin: 0 auto;
    display: flex;
  }

  .listing_carousel_box1,
  .listing_carousel_box2,
  .listing_carousel_box3 {
    width: 868vw;
    max-width: 350px;
    height: 120px;
    border-radius: 28px;
    margin: 0 auto;
    box-shadow: 0 2px 8px rgba(0,0,0,0.07);
    display: flex;
    align-items: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 1px solid rgb(168, 168, 168);
    transition: box-shadow 0.3s;
  }

  .listing_carousel_box1:active,
  .listing_carousel_box2:active,
  .listing_carousel_box3:active {
    box-shadow: 0 4px 16px rgba(0,150,209,0.10);
  }

  .listing_carousel_box4,
  .listing_carousel_box5,
  .listing_carousel_box6,
  .listing_carousel_box7,
  .listing_carousel_box8 {
    display: none;
  }

  .text_boxes_1,
  .text_boxes_2,
  .text_boxes_3,
  .listing_contador_number {
    display: none;
  }
}